*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}
body {
  position: relative;
}
.drawer {
  position: absolute;
  top: 30px;
  width: 300px;
  height: 100vh;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.sidebar {
  height: 100%;
  background-color: #3f549c;
  color: #faf9f9;
  background-size: cover;
  background-repeat: no-repeat;
}
.sidebar-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar-item-content {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: flex;
  align-items: center;
  width: 100%;
}

.sidebar-item-icon {
  margin-right: 6px;
}

.sidebar-item-text {
  width: 100%;
}

.sidebar-item-expand-arrow {
  font-size: 1.2rem !important;
}

.sidebar-item-expand-arrow-expanded {
  font-weight: bold;
}
